/**
 * 按需加载 element-ui 组件
 * @description
 * [参考官网 - 按需加载](https://element.eleme.cn/#/zh-CN/component/quickstart#an-xu-yin-ru)
 */
import Vue from 'vue';
import {
  Button,
  Input,
  Select,
  Option,
  Tree,
  Row,
  Col,
  Message,
  Loading,
  Dropdown,
  DropdownMenu,
  DropdownItem,
  Radio,
  RadioGroup,
  Slider
} from 'element-ui';

Vue.use(Button);
Vue.use(Input);
Vue.use(Select);
Vue.use(Option);
Vue.use(Tree);
Vue.use(Row);
Vue.use(Col);
Vue.use(Loading.directive);
Vue.use(Dropdown);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(Radio);
Vue.use(RadioGroup);
Vue.use(Slider);

Vue.prototype.$loading = Loading.service;
Vue.prototype.$message = Message;
